@import './public';
#partDetial{
  .sro{
    overflow-y: auto;
    width: 828px;
    height: 398px;
  }
  .sro::-webkit-scrollbar-thumb {
    border-radius: 10px;
    width: 8px;
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
    background-color:#026086;
  }
  .example{
    margin-top: 10px;
  }
  .content {
    position: relative;
    border: 1px solid #001721;
    box-shadow: 0px 0px 40px #0e4760 inset;
    padding:15px;
    margin-top:10px;
    background-color:rgba(0,17,27,0.8);
    .company-name{
      padding: 24px 0;
      @include whiteSpace;
    }
    .change{
      padding: 5px 0 16px 0;
      color:#ff8b1b;
      @include whiteSpace;
    }
    .dishonesty-content{
      overflow: hidden;
      li{
        float:left;
        width:50%;
        @include whiteSpace;
        span{
          color:#05adeb;
        }
      }
      li:not(:last-child){
        padding-bottom: 20px;
      }
    }
    .theme{
      padding-top: 34px;
      line-height:26px;
      li{
        padding-bottom:15px;
        @include whiteSpace;
      }
      li:first-child{
        font-size:16px;
        font-weight:bold;
      }
      li:last-child{
        color:#00affd;
        i{
          padding-left:10px;
        }
      }
    }
    .theme-content{
      padding-top: 20px;
      li{
        margin-bottom:8px;
        @include  whiteSpace;
      }
      .sys{
        line-height:26px;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 3;
        box-orient: vertical;
        line-clamp: 3;
        white-space: normal;
        overflow: hidden;
      }
      em{
        color:#00a1dd;
      }
      li:first-child{
        border-left:2px solid #00a1dd;
        color:#00a1dd;
        text-indent: 10px;
      }
    }
    .typ{
      position: absolute;
      top:0;
      left:24px;
      padding:0px 5px;
      text-align: center;
      background-image:-webkit-linear-gradient(45deg,#cc750c,#c61100);
      background-image:linear-gradient(45deg,#cc750c,#c61100);
      height: 20px;
      line-height: 20px;
      border-radius: 0px 0px 5px 5px;
    }
    .Indictment-content{
      overflow: hidden;
      li{
        float:left;
        width:50%;
        @include whiteSpace;
        span{
          color:#05adeb;
          font-family: "微软雅黑";
        }
      }
      li:not(:last-child){
        padding-bottom: 20px;
      }
    }
    .special-name{
      padding:6px 0 18px 0;
      i{
        color:#02bed6;
        font-weight:600;
      }
    }
    .typ_base {
      position: absolute;
      top: 0;
      left: 24px;
      text-align: center;
      height: 20px;
      line-height: 20px;
      border-radius: 0px 0px 5px 5px;
      padding:0px 5px;
    }
    .typ1{
      background-image:-webkit-linear-gradient(45deg,#cc750c,#c61100);
      background-image:linear-gradient(45deg,#cc750c,#c61100);
    }
    .typ2{
      background-image:-webkit-linear-gradient(45deg,#009fd7,#0064d5);
      background-image:linear-gradient(45deg,#009fd7,#0064d5);

    }
    .corporate-ul{
      overflow: hidden;
      li{
        float:left;
        width:50%;
        @include whiteSpace;
        span{
          color:#05adeb;
        }
      }
      li:not(:last-child){
        padding-bottom: 20px;
      }
    }
  }
}


