@import './public';
#showInfo{
  position:relative;
  box-sizing: border-box;
  padding: 25px 20px;
  width:900px;
  height:820px;
  background:rgba(7,20,32,0.5);
  float:right;
  .example{
    padding-bottom: 15px;
    border:1px solid transparent;
  }
  .example:hover{
    cursor: pointer;
    background-color: #0d2131;
    -webkit-box-shadow: inset 0 0 30px rgba(18,77,109,0.6);
   box-shadow: inset 0 0 30px rgba(18,77,109,0.6);
    border:1px solid #046dc5;
  }
  .tubiao{
    position: absolute;
    top:154px;
    width:700px;
    height:600px;
    background:url(../img/showInfo_bg.png) no-repeat;
  }
  .show{
    position: absolute;
    top:140px;
    overflow: hidden;
    z-index:2;
    button{
      outline: none;
      text-align: center;
      cursor: pointer;
      color:#03e4ff;
      margin-left:12px;
      font-size:12px;
      border-radius: 20px;
      background-color:transparent;
      width:100px;
      height:30px;
    }
    .only-show{
      border:1px solid rgba(5,73,110,1);
      background-image:linear-gradient(to top,rgba(11,43,74,0.6),rgba(9,28,53,0.6));
    }
    .only-show:hover{
      cursor: pointer;
      color:#04b8d2;
      border:1px solid #2d6381;
    }
    .all-show{
      border:1px solid #0091d5;
      color:#04bbd4;
      background-image:linear-gradient(to top,rgba(9,65,109,0.6),rgba(11,33,66,0.6));
    }
    .all-show:hover{
      cursor: pointer;
      border:1px solid #0372a8;
      background-image:linear-gradient(to top,rgba(9,57,95,0.6),rgba(11,30,61,0.6));
    }
  }
  .relationGraph{
    width: 608px;
    height: 600px;
  }
  .info{
    width: 254px;
    max-height: 694px;
    position: absolute;
    right:20px;
    top:110px;
    border:1px solid #0e1926;
    background-color: rgba(20,31,44,0.8);
    overflow: auto;
    p{
      height: 40px;
      width: 100%;
      background-image:linear-gradient(to top,rgb(11,46,66),rgb(15,49,69));
      font-size: 14px;
      line-height:40px;
      text-indent: 20px;
      @include whiteSpace;
    }
    ul{
      text-indent: 30px;
      padding:5px 0;
      li{
        padding:12px 0;
        span{
          display: inline-block;
          float:left;
          width:136px;
          color:#09b2f0;
        }
        i{
          display: inline-block;
          float:left;
        }
      }

    }
  }
  .info::-webkit-scrollbar-track{
    border-radius: 10px;
    background-color: #051c26;
  }
  .info::-webkit-scrollbar-thumb {
    border-radius: 10px;
    width: 8px;
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
    background-color:#026086;
  }
}
.big-change{
  display:flex;
  justify-content: space-between;
  .dl{
    box-shadow: 0px 0px 40px #0e4760 inset;
  }
  dl{
    cursor: pointer;
    box-shadow: 0px 0px 40px rgba(16,75,101,0.8) inset;
    width: 158px;
    height: 70px;
    flex:1;
    margin-left:5px;
    border-radius: 5px;
    background-color:rgba(12,51,83,0.6);
    border:2px solid transparent;
    border-image: -webkit-gradient(linear, 0 0, 100% 0, from(#0376db), color-stop(20%, rgba(0,0,0,0)), color-stop(80%, rgba(0,0,0,0)) , to(#0376db)) 10 10;
  }

  dl:hover{
    cursor: pointer;
    box-shadow: 0px 0px 66px rgba(19,88,110,1.1) inset;
  }
  dt{
    float:left;
    width: 46px;
    text-align: center;
    padding: 10px 0;
  }
  dd{
    width:110px;
    float:left;
    padding:10px 0;
    font-size:12px;
    .shy{
      clear:both;
      padding-top:10px;
      span{
        float:left;
      }
      em{
        float:right;
        padding-left:3px;
        width:30px;
        overflow: hidden;
      }
    }
  }

  .litigation-info{
    float:left;
    line-height:20px;
    font-weight:bold;
    color:#038ec5;
  }
  .litigation-count{
    text-align: right;
    float:right;
    width:40px;
    overflow: hidden;
    padding-right:5px;
    padding-Left:4px;
    font-size: 20px;
    font-family:Arial;
    font-weight:bold;
    color:#03e4ff;
  }
}


